.sprite,
.sprite::before,
.sprite::after {
	background-image: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/38273/yt-sprite-01.webp);
	background-repeat: no-repeat;
}

.sprite-alt,
.sprite-alt::before,
.sprite-alt::after {
	background-image: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/38273/yt-sprite-02.webp);
	background-repeat: no-repeat;
}

.details-header .actions {
	clear: left;
}

.details-header .actions ul {
	padding-top: 0.9231em;
	list-style-type: none;
}

.details-header .actions a {
	margin-left: 1.8181em;
	color: #000;
	font-size: 0.8462em;
	text-decoration: none;
	background: none;
	opacity: 0.5;
}

.details-header .actions a:hover { opacity: 0.6; }

.details-header .actions .more {
	margin: 0;
	line-height: inherit;
	border: none;;
}

.details-header .actions a::before {
	content: '';
	display: inline-block;
	vertical-align: middle;
	width: 20px;
	height: 20px;
	margin-right: 0.5455em;
}

.sentiment .views {
	position: absolute;
	bottom: 100%;
	right: 0;
	width: 160px;
	color: #666;
	font-size: 1.4615em;
	line-height: 2em;
	text-align: right;
}

.sentiment .views::after {
	content: '';
	position: absolute;
	bottom: 0;
	display: block;
	width: 100%;
	height: 2px;
	background-color: #167ac6;
}

ul.sentiment {
	padding: 0 !important;
}

ul.sentiment li {
	padding-bottom: 0 !important;
	margin-bottom: 0 !important;
}

.details .sentiment a { margin-right: 0.9091em; }

.sentiment .like::before { background-position: -58px -28px; }

.sentiment .dislike::before { background-position: -187px 0; }


