.feedback-button {
	cursor: pointer;
	height:40px;
	border:solid 1px #CCCCCC;
	background: #1f2018;
	width:140px;
	line-height:32px;
	-webkit-transform:rotate(-90deg);
	font-weight:600;
	color: #8cbe71;
	transform:rotate(-90deg);
	-ms-transform:rotate(-90deg);
	-moz-transform:rotate(-90deg);
	text-align:center; font-size:17px;
	position:fixed; right:-55px; top:45%;
	z-index:999;
}

.hiddenfeedback{
	display:none;
	padding-top:0px;
}

.displayfeedback {
	display: block;
	position: absolute;
	height: calc(100% - 65px);
	width: 100%;
	z-index: 265;
	background-color: rgba(31, 32, 24, 0.3);
}

.feedback-main-block{
	position: fixed;
	top: 15%;
}

#feedback-div {
	background-color: #1f2018;
	padding: 35px;
	-webkit-box-shadow: 2px 2px 5px 2px #000;
	        box-shadow: 2px 2px 5px 2px #000;
	width: 450px;
	position: fixed;
	top: 15%;
	-moz-border-radius: 3px;
	-webkit-border-radius: 3px;
	z-index: 999;
}

.feedback-input {
	color:#3c3c3c;
	font-weight:500;
	font-size: 16px;
	border-radius: 0;
	line-height: 22px;
	background-color: #fbfbfb;
	padding: 13px 13px 13px 54px;
	margin-bottom: 10px;
	width:100%;
	-webkit-box-sizing: border-box;
	-ms-box-sizing: border-box;
	box-sizing: border-box;
	border: 2px solid #8cbe71;
}

.feedback-input:focus{
	background: #fff;
	-webkit-box-shadow: 0;
	        box-shadow: 0;
	border: 2px solid #8cbe71;
	color: #8cbe71;
	outline: none;
	padding: 13px 13px 13px 54px;
}

/* Icons ---------------------------------- */
#feedback-name{
	background-image: url(http://rexkirby.com/kirbyandson/images/name.svg);
	background-size: 30px 30px;
	background-position: 11px 8px;
	background-repeat: no-repeat;
}

#feedback-name:focus{
	background-image: url(http://rexkirby.com/kirbyandson/images/name.svg);
	background-size: 30px 30px;
	background-position: 8px 5px;
	background-position: 11px 8px;
	background-repeat: no-repeat;
}

#feedback-email{
	background-image: url(http://rexkirby.com/kirbyandson/images/email.svg);
	background-size: 30px 30px;
	background-position: 11px 8px;
	background-repeat: no-repeat;
}

#feedback-email:focus{
	background-image: url(http://rexkirby.com/kirbyandson/images/email.svg);
	background-size: 30px 30px;
	background-position: 11px 8px;
	background-repeat: no-repeat;
}

#feedback-comment{
	background-image: url(http://rexkirby.com/kirbyandson/images/comment.svg);
	background-size: 30px 30px;
	background-position: 11px 8px;
	background-repeat: no-repeat;
}

#feedback-comment {
	width: 100%;
	height: 150px;
	line-height: 150%;
	resize:vertical;
}

.feedback-input:hover, #feedback-comment:hover, .feedback-input:focus, #feedback-comment:focus {
	background-color:white;
}

#feedback-button-blue{
	float:left;
	width: 100%;
	border: #8cbe71 solid 4px;
	cursor:pointer;
	background-color: #1f2018;
	color: #8cbe71;
	font-size:24px;
	padding-top:22px;
	padding-bottom:22px;
	-webkit-transition: all 0.3s;
	-o-transition: all 0.3s;
	transition: all 0.3s;
	margin-top:-4px;
	font-weight:700;
}

#feedback-button-blue:hover{
	background-color: rgba(0,0,0,0);
	color: #000;
}

.feedback-button-blue:hover {
	color: #000;
}

.feedback-ease {
	width: 0px;
	height: 80px;
	background-color: #fbfbfb;
	-webkit-transition: .3s ease;
	-o-transition: .3s ease;
	transition: .3s ease;
}

.feedback-submit:hover .feedback-ease{
	width:100%;
	background-color: #8cbe71;
}

.icon-close {
	background-color: silver;
	padding: 5px;
	border-radius: 50%;
	color: white;
	position: absolute;
	top: 7px;
	right: 7px;
	opacity: 0.5;
	cursor: pointer;
	width: 24px;
	height: 24px;
	-webkit-transition: opacity 0.5s;
	-o-transition: opacity 0.5s;
	transition: opacity 0.5s;
}

.icon-close:hover {
	opacity: 1;
}

@media only screen and (max-width: 580px) {
	#feedback-div{
		left: 3%;
		margin-right: 3%;
		width: 88%;
		margin-left: 0;
		padding-left: 3%;
		padding-right: 3%;
	}
}


