.lang-table {
	padding: 0 !important;
	position: absolute;
	top: 2px;
	left: 6px;
}

.lang-table > div {
	cursor: pointer;
	height: 18px;
	margin-top: 0px;
}

/*  user notify dropdown simple */

.dropdown2 {
	display: inline-block;
	right: 50px;
	z-index: 999;
}

.dropdown2-hover {
	position: relative;
	top: 0;
	left: 0;
}

.dropdown2-overlay {
	display: none;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 1;
}

.dropdown2-inner {
	position: absolute;
	top: 61px;
	right: -90px;
	width: 200px;
	opacity: 0;
	visibility: hidden;
	z-index: 2;
}

dropdown2-inner::before {
	content: '';
	width: 0;
	height: 0;
	border-top: 12px solid #727272;
	border-bottom: 12px solid #727272;
	border-right: 12px solid #727272;
}

.dropdown2:hover .dropdown2-inner,
.dropdown2-open:checked ~ .dropdown2-inner {
	opacity: 1;
	visibility: visible;
}

.dropdown2:hover ~ .dropdown2-overlay,
.dropdown2-open:checked ~ .dropdown2-overlay {
	display: block;
}

/* Demo only */

