/* switchbtnforcomment */
.checkbox-switch {
	display: none;
}

.checkbox-switch + label {
	display: inline-block;
	position: relative;
	top: 12px;
	width: 100px;
	height: 40px;
	background: -webkit-gradient(linear, left top, right top, color-stop(0, #fd1a15), color-stop(45%, #fc8b34), color-stop(55%, #7fd03e), to(#007177)) 0 0;
	background: -webkit-linear-gradient(left, #fd1a15 0, #fc8b34 45%, #7fd03e 55%, #007177 100%) 0 0;
	background: -o-linear-gradient(left, #fd1a15 0, #fc8b34 45%, #7fd03e 55%, #007177 100%) 0 0;
	background: linear-gradient(to right, #fd1a15 0, #fc8b34 45%, #7fd03e 55%, #007177 100%) 0 0;
	background-size: 210px 30%;
	border-radius: 20px;
	padding: 2px;
	margin: 0 0 0 5px;
	cursor: pointer;
	-webkit-transition: all 500ms ease;
	-o-transition: all 500ms ease;
	transition: all 500ms ease;
	-webkit-box-shadow: 0 0px 10px rgba(0, 0, 0, 0.2) inset;
	box-shadow: 0 0px 10px rgba(0, 0, 0, 0.2) inset;
}
.checkbox-switch + label:before {
	content: "No";
	width: 37px;
	height: 37px;
	position: absolute;
	left: 7px;
	top: 1.6px;
	border-radius: 100%;
	background: #fff;
	text-align: center;
	line-height: 40px;
	font-family: "Cabin", sans-serif;
	font-size: 12px;
	font-weight: bold;
	color: #fd1a15;
	letter-spacing: 3px;
	text-transform: uppercase;
	-webkit-transition: left 500ms ease, color 500ms ease, -webkit-transform 150ms ease;
	transition: left 500ms ease, color 500ms ease, -webkit-transform 150ms ease;
	-o-transition: left 500ms ease, color 500ms ease, transform 150ms ease;
	transition: left 500ms ease, color 500ms ease, transform 150ms ease;
	transition: left 500ms ease, color 500ms ease, transform 150ms ease, -webkit-transform 150ms ease;
}
.checkbox-switch + label:active:before {
	-webkit-transform: scale(0.95);
	-ms-transform: scale(0.95);
	    transform: scale(0.95);
}

.checkbox-switch:checked + label {
	background-position-x: -110px;
}
.checkbox-switch:checked + label:before {
	content: "Yes";
	color: #007177;
	left: 51px;
}

