article, aside, figure, footer, header, hgroup, menu, nav, section { display: block }

section {
	background: rgba(255,255,255,0.9);
	padding-top: 20px;
	padding-bottom: 20px;
	height: 100%;
	margin: auto;
}

.comment {
	overflow: hidden;
	padding: 0 0 1em;
	border-bottom: 1px solid #ddd;
	margin: 0 0 1em;
	*zoom: 1;
	width: 100%;
}

.comment-img {
	float: left;
	margin-right: 33px;
	overflow: hidden;
}

.comment-img img { display: block }

.comment-body {
	overflow: hidden;
	word-wrap: break-word;
}

.comment .text {
	padding: 10px;
	border: 1px solid #e5e5e5;
	border-radius: 3px;
	background: #fff;
}

.comment .text p:last-child { margin: 0 }

.comment .attribution {
	margin: 0.5em 0 0;
	font-size: 14px;
	color: #666;
}

/* Decoration */

.comments, .comment { position: relative }

.comments:before, .comment:before, .comment .text:before {
	content: "";
	position: absolute;
	top: 0;
	left: 65px;
}

.comments:before {
	width: 3px;
	left: 63px;
	bottom: 0px;
	background: rgba(0,0,0,0.1);
}

.comment:before {
	width: 9px;
	height: 9px;
	border: 3px solid #fff;
	border-radius: 100px;
	margin: 16px 0 0 -6px;
	-webkit-box-shadow: 0 1px 1px rgba(0,0,0,0.2), inset 0 1px 1px rgba(0,0,0,0.1);
	        box-shadow: 0 1px 1px rgba(0,0,0,0.2), inset 0 1px 1px rgba(0,0,0,0.1);
	background: #ccc;
}

.comment:hover:before { background: #3b5998 }

.comment .text:before {
	top: 18px;
	left: 78px;
	width: 9px;
	height: 9px;
	border-width: 0 0 1px 1px;
	border-style: solid;
	border-color: #e5e5e5;
	background: #fff;
	-webkit-transform: rotate(45deg);
	-moz-transform: rotate(45deg);
	-ms-transform: rotate(45deg);
	-o-transform: rotate(45deg);
}

.comicon {
	display: inline-block !important;
	cursor: pointer;
}

.all-comments {
	border: 1px solid rgb(221, 221, 221);
	border-radius: 3px;
	height: 44px;
}

.all-comments > span {
	line-height: 44px;
}

.empty-field {
	border: 3px solid rgba(130, 0, 0, 0.67) !important;
}

.empty-field-feedback {
	border: 3px solid rgba(130, 0, 0, 0.67) !important;
	padding: 12px 0 0 50px;
}

/* popup */

.mm-popup {
	display: none;
}

.mm-popup--visible {
	display: block;
}

.mm-popup__overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1000;
	overflow: auto;
	background: rgba(0, 0, 0, .1);
}

.mm-popup__close {
	position: absolute;
	top: 12px;
	right: 20px;
	padding: 0;
	width: 24px;
	height: 24px;
	cursor: pointer;
	outline: none;
	text-align: center;
	border-radius: 10px;
	border: none;
	text-indent: -9999px;
	background: transparent url(../../../lib/img/close-popup.png) no-repeat center center;
	background-size: 100%;
	margin: 0;
	-webkit-transition: -webkit-transform 0.3s ease-in-out;
	transition: -webkit-transform 0.3s ease-in-out;
	-o-transition: transform 0.3s ease-in-out;
	transition: transform 0.3s ease-in-out;
	transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.mm-popup__close:hover {
	-webkit-transform: rotate(180deg);
	    -ms-transform: rotate(180deg);
	        transform: rotate(180deg);
}

.mm-popup__input {
	display: block;
	width: 100%;
	height: 30px;
	border-radius: 3px;
	background: #f5f5f5;
	border: 1px solid #e9ebec;
	outline: none;
	-webkit-box-sizing: border-box !important;
	box-sizing: border-box !important;
	font-size: 14px;
	padding: 0 12px;
	color: #808080;
}

.mm-popup__btn {
	border-radius: 3px;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	padding: 0 10px;
	margin: 0;
	line-height: 32px;
	height: 32px;
	border: 1px solid #666;
	text-align: center;
	display: inline-block;
	font-size: 12px;
	color: #8cbe71;
	background: #1f2018;
	outline: none;
	text-decoration: none;
	cursor: pointer;
}

.mm-popup__btn--success {
	background-color: #27ae60;
	border-color: #27ae60;
	color: #fff;
}

.mm-popup__btn--danger {
	background-color: #c5545c;
	border-color: #c5545c;
	color: #fff;
}

.mm-popup__box {
	width: 350px;
	position: fixed;
	top: 10%;
	left: 50%;
	margin-left: -175px;
	background: #fff;
	-webkit-box-shadow: 0px 5px 20px 0px rgba(126,137,140,0.20);
	        box-shadow: 0px 5px 20px 0px rgba(126,137,140,0.20);
	border-radius: 5px;
	border: 1px solid #111111;
	overflow: hidden;
	z-index: 1001;
}

.mm-popup__box__header {
	padding: 15px 20px;
	background: #1f2018;
	color: #8cbe71;
}

.mm-popup__box__header__title {
	margin: 0;
	font-size: 16px;
	text-align: left;
	font-weight: 600;
}

.mm-popup__box__body {
	padding: 20px;
	line-height: 1.4;
	font-size: 14px;
	color: #454B4D;
	background: #fff;
	position: relative;
	z-index: 2;
}

.mm-popup__box__body p {
	margin: 0 0 5px;
}

.mm-popup__box__footer {
	overflow: hidden;
	padding: 40px 20px 20px;
}

.mm-popup__box__footer__right-space {
	float: right;
}

.mm-popup__box__footer__right-space .mm-popup__btn {
	margin-left: 5px;
}

.mm-popup__box__footer__left-space {
	float: left;
}

.mm-popup__box__footer__left-space .mm-popup__btn {
	margin-right: 5px;
}

.mm-popup__box--popover {
	width: 300px;
	margin-left: -150px;
}

.mm-popup__box--popover .mm-popup__close {
	position: absolute;
	top: 5px;
	right: 5px;
	padding: 0;
	width: 20px;
	height: 20px;
	cursor: pointer;
	outline: none;
	text-align: center;
	border-radius: 10px;
	border: none;
	text-indent: -9999px;
	background: transparent url('data:image/png;base64,') no-repeat center center;
	background-size: 100%;
	margin: 0;
	z-index: 3;
}

.mm-popup__box--popover .mm-popup__box__body {
	padding: 20px;
}

@media (max-width: 420px) {
	.mm-popup__box {
		width: auto;
		left: 10px;
		right: 10px;
		top: 10px;
		margin-left: 0;
	}

	.mm-popup__box__footer__left-space {
		float: none;
	}

	.mm-popup__box__footer__right-space {
		float: none;
	}

	.mm-popup__box__footer {
		padding-top: 30px;
	}

	.mm-popup__box__footer .mm-popup__btn {
		display: block;
		width: 100%;
		text-align: center;
		margin-top: 10px;
	}
}

.toolbar-disable .rdw-editor-toolbar {
	display: none;
}


