.header {
  background-color: #1f2018;
  height: 65px;
}

.logo {
  width: 250px;
}

.logo-icon {
  width: 65px;
  height: 65px;
  position: absolute;
  top: 0px;
  left: 0px;
}

.fb-icon, .gt-icon {
  width: 37px;
  height: 40px;
  padding-bottom: 5px;
  border-bottom: 2px solid transparent;
  cursor: pointer;
}

.fb-icon:hover, .gt-icon:hover {
  border-bottom: 2px solid #8cbe71;
}

.rubik-wrapper {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}

.rubik-loader {
	width: 64px;
	height: 64px;
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	-ms-transform: translate(-50%, -50%);
	    transform: translate(-50%, -50%);
	background-image: url(http://i.giphy.com/3og0ISeflb7vrNzy2A.gif);
	background-repeat: no-repeat;
	background-position: center;
}

.face {
	font-size: 6.5rem;
}

.hand {
	font-size: 10rem;
	display: inline-block;
	-webkit-animation: wee 1s infinite ease-in-out alternate backwards;
	animation: wee 1s infinite ease-in-out alternate backwards;
	margin-right: -3rem;
}

@-webkit-keyframes wee {
	from {
		-webkit-transform: rotate(20deg);
		transform: rotate(20deg);
	}
	to {
		-webkit-transform: rotate(-20deg);
		transform: rotate(-20deg);
	}
}

@keyframes wee {
	from {
		-webkit-transform: rotate(20deg);
		transform: rotate(20deg);
	}
	to {
		-webkit-transform: rotate(-20deg);
		transform: rotate(-20deg);
	}
}

/* footer */

div.footer > div > footer {
	margin: 0 auto;
	width: 100%;
	background: #ffffff;
	border-top: 0.0625em solid lightgrey;
	bottom: 0;
	position: absolute;
	color: #000000;
	height: 60px;
}

div.footer > div > footer > p {
	text-align: center;
	width: 100%;
	line-height: 55px;
	margin: 0px;
}

/* burger nav menu */

.nav-mobile {
	background-color: #1f2018;
	height: 65px;
}

.nav-mobile-menu {
	background-color: #1f2018;
}

.nav-mobile-menu span {
	color: #8cbe71;
	font-family: Pompadur !Important;
}

.nav-menu div {
	border-bottom: 1px solid rgba(77, 77, 77, 0.53);
	padding: 5px 0;
}

.nav-menu div:first-child {
	border-top: 1px solid rgba(77, 77, 77, 0.53);
	padding: 5px 0;
}

.user-span {
	padding: 5px 0;
	color: silver;
	font-weight: bold;
	font-size: 24px;
	cursor: pointer;
}

.burger-menu {
	position: relative;
	width: 50px;
	height: 50px;
	cursor: pointer;
}
.burger {
	position: absolute;
	background: rgba(111,111,111,1);
	width: 50%;
	height: 5px;
	top: 50%;
	right: 0px;
	margin-top: -5px;
	opacity: 1;
}
.burger::before {
	position: absolute;
	background: rgba(111,111,111,1);
	width: 25px;
	height: 5px;
	top: 10px;
	content: "";
	display: block;
}
.burger::after {
	position: absolute;
	background: rgba(111,111,111,1);
	width: 25px;
	height: 5px;
	bottom: 10px;
	content: "";
	display: block;
}
.burger::after, .burger::before, .burger {
	-o-transition: all .3s ease-in-out;
	transition: all .3s ease-in-out;
	-webkit-transition: all .3s ease-in-out;
}
.burger-menu.menu-on .burger::after{
	-ms-transform: rotate(-45deg);
	    transform: rotate(-45deg);
	-webkit-transform: rotate(-45deg);
	bottom: 0px;
}
.burger-menu.menu-on .burger::before{
	-ms-transform: rotate(45deg);
	    transform: rotate(45deg);
	-webkit-transform: rotate(45deg);
	top: 0px;
}
.burger-menu.menu-on .burger{
	background: rgba(111,111,111,.0);
}

@media screen and (max-width: 900px) {
	.logo {
		margin-left: 50px;
	}
}


