@import url(https://code.ionicframework.com/ionicons/2.0.1/css/ionicons.min.css);
figure.snip1253 {
	color: #fff;
	position: relative;
	float: left;
	overflow: hidden;
	margin: 10px 0;
	min-width: 250px;
	max-width: 310px;
	width: 100%;
	background-color: #ffffff;
	color: #000000;
	text-align: left;
	font-size: 16px;
	-webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
	        box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
}
figure.snip1253 * {
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	-webkit-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
	transition: all 0.3s ease;
}
figure.snip1253 .image {
	max-height: 220px;
	overflow: hidden;
}
figure.snip1253 img {
	max-width: 100%;
	vertical-align: top;
	position: relative;
}
figure.snip1253 figcaption {
	margin: -40px 15px 0;
	padding: 15px ;
	position: relative;
	background-color: #ffffff;
}
figure.snip1253 .date {
	background-color: #8cbe71;
	top: 15px;
	color: #fff;
	left: 15px;
	min-height: 48px;
	min-width: 48px;
	position: absolute;
	text-align: center;
	font-size: 20px;
	font-weight: 700;
	text-transform: uppercase;
}
figure.snip1253 .date span {
	display: block;
	line-height: 24px;
}
figure.snip1253 .date .month {
	font-size: 14px;
	background-color: rgba(0, 0, 0, 0.1);
}
figure.snip1253 h3,
figure.snip1253 p {
	margin: 0;
	padding: 0;
}
figure.snip1253 h3 {
	min-height: 50px;
	margin-bottom: 10px;
	margin-left: 60px;
	display: inline-block;
	font-weight: 600;
	text-transform: uppercase;
}
figure.snip1253 p {
	font-size: 0.8em;
	margin-bottom: 20px;
	line-height: 1.6em;
}
figure.snip1253 footer {
	padding: 0 25px;
	background-color: #1f2018;
	color: #e6e6e6;
	font-size: 0.8em;
	line-height: 30px;
	text-align: right;
}
figure.snip1253 footer > div {
	display: inline-block;
	margin-left: 10px;
}
figure.snip1253 footer i {
	color: rgba(255, 255, 255, 0.2);
	margin-right: 5px;
}
figure.snip1253 a {
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	position: absolute;
	z-index: 1;
}
figure.snip1253:hover img,
figure.snip1253.hover img {
	-webkit-transform: scale(1.15);
	-ms-transform: scale(1.15);
	    transform: scale(1.15);
}

figcaption h3 {
	max-width: 200px;
	word-wrap: break-word;
}


.btnnn {
	position: relative;
	display: inline-block;
	margin: 30px 10px;
	padding: 0;
	font-weight: bold ;
	overflow: hidden;

	border-width: 0;
	outline: none;
	border-radius: 2px;
	-webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, .6);
	        box-shadow: 0 1px 4px rgba(0, 0, 0, .6);

	background-color: #1c1d17;
	color: #8cbe71;

	-webkit-transition: background-color .3s;

	-o-transition: background-color .3s;

	transition: background-color .3s;
}

.btnnn:hover, .btnnn:focus {
	background-color: #27ae60;
	color: white;
	cursor: pointer;
}

.btnnn > * {
	position: relative;
}

.btnnn span {
	display: block;
	padding: 4px 8px;
}

.btnnn:before {
	content: "";
	position: absolute;
	top: 50%;
	left: 50%;
	display: block;
	width: 0;
	padding-top: 0;
	border-radius: 100%;
	background-color: rgba(236, 240, 241, .3);
	-webkit-transform: translate(-50%, -50%);
	-ms-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
}

.btnnn:active:before {
	width: 120%;
	padding-top: 120%;
	-webkit-transition: width .2s ease-out, padding-top .2s ease-out;
	-o-transition: width .2s ease-out, padding-top .2s ease-out;
	transition: width .2s ease-out, padding-top .2s ease-out;
}


.btnnn.orange {
	background-color: #e67e22;
}

.btnnn.orange:hover, .btnnn.orange:focus {
	background-color: #d35400;
}

.btnnn.red {
	background-color: #1f2018;
}

.btnnn.red:hover, .btnnn.red:focus {
	background-color: #c0392b;
	color: white;
	cursor: pointer;
}

.dropzone {
	height: 107px;
}

.dropzone>span {
	color: silver;
	cursor: pointer;
	line-height: 107px;
}

.rdw-editor-main {
	-webkit-box-shadow: inset 0px 2px 5px -3px rgba(0,0,0,0.75);
	        box-shadow: inset 0px 2px 5px -3px rgba(0,0,0,0.75);
	padding: 10px;
}

.error-table {
	position: absolute;
}

.error-span {
	color: rgba(242, 85, 86, 0.89);
}

.content img {
	display:block;
	margin:auto;
}


@media screen and (min-width: 1200px) {
	.panel {
		position: fixed;
	}
}

.rdw-editor-toolbar {
	-ms-flex-pack: center !important;
	    justify-content: center !important;
	border: 1px solid rgb(221, 221, 221) !important;
}

.rdw-editor-main {
	min-height: 400px !important;
	border-left: 1px solid rgb(221, 221, 221) !important;
	border-right: 1px solid rgb(221, 221, 221) !important;
	border-bottom: 1px solid rgb(221, 221, 221) !important;
	border-radius: 3px !important;
}

.rdw-option-active {
	background-color: rgba(189, 189, 189, 0.18) !important;
}

.rdw-option-wrapper {
	border: none !important;
}

/* input tags */
div.ReactTags__tags {
	position: relative;
}

/* Styles for the input */
div.ReactTags__tagInput {
	border-radius: 3px;
	display: inline-block;
}

input.ReactTags__tagInputField {
	height: 35px;
	margin: 0;
	font-size: 16px;
	background-color: #1f2018;
	border-radius: 3px;
	border: none;
	padding: 3px 0 3px 15px;
}

div.ReactTags__tagInput input.ReactTags__tagInputField:focus {
	height: 35px;
	margin: 0;
	font-size: 16px;
}

/* Styles for selected tags */
div.ReactTags__selected span.ReactTags__tag {
	background: #1f2018;
	font-size: 16px;
	color: #8cbe71;
	display: inline-block;
	padding: 6px;
	margin: 5px 5px;
	cursor: move;
	border-radius: 3px;
}
div.ReactTags__selected a.ReactTags__remove {
	color: #aaa;
	margin-left: 5px;
	cursor: pointer;
}

/* Styles for suggestions */
div.ReactTags__suggestions {
	position: absolute;
}
div.ReactTags__suggestions ul {
	list-style-type: none;
	-webkit-box-shadow: .05em .01em .5em rgba(0,0,0,.2);
	        box-shadow: .05em .01em .5em rgba(0,0,0,.2);
	background: white;
	width: 200px;
}
div.ReactTags__suggestions li {
	border-bottom: 1px solid #ddd;
	padding: 5px 10px;
	margin: 0;
}
div.ReactTags__suggestions li mark {
	text-decoration: underline;
	background: none;
	font-weight: 600;
}
div.ReactTags__suggestions ul li.ReactTags__activeSuggestion {
	background: #b7cfe0;
	cursor: pointer;
}


