.user-info {
	border-radius: 3px;
}

.profile-data {
	color: #be9224;
}

.profile-input {
	border: none;
	color: black;
	width: 300px;
	background-color: transparent;
	color: #be9224;
}

.profile-input:focus {
	background-color: transparent !important;
	-webkit-box-shadow: none !important;
	        box-shadow: none !important;
	color: black !important;
}

.profile-input:active {
	-webkit-box-shadow: none !important;
	        box-shadow: none !important;
	background-color: transparent !important;
	color: black !important;
}

.statix {
	display: inline-block;
	width: 200px;
	margin: 0 10px;
}

.PublicProfile {
	position: fixed;
	word-wrap: break-word;
}

.done-img {
	cursor: pointer;
	position: absolute;
	bottom: 70px;
	left: 100px;
}

.cancel-img {
	cursor: pointer;
	position: absolute;
	bottom: 70px;
	left: 130px;
}

/* profile */

.profile-bg-hidden {
	display: none;
}

.profile-bg {
	display: block;
	position: absolute;
	height: calc(100% - 65px);
	width: 100%;
	z-index: 250;
	background-color: rgba(31, 32, 24, 0.3);
}

.profile-card {
	position: fixed;
	top: 15%;
}

.icon-camera {
	background-color: silver;
	padding: 5px;
	border-radius: 50%;
	color: white;
	position: absolute;
	top: 10px;
	left: 10px;
	opacity: 0.5;
	width: 42px;
	height: 42px;
	-webkit-transition: opacity 0.5s;
	-o-transition: opacity 0.5s;
	transition: opacity 0.5s;
}

.icon-camera:hover {
	cursor: pointer;
	opacity: 1;
}

.upload-image {
	position: absolute;
	top: 15%;
	left: 30%;
	background-color: white;
	border: 1px solid silver;
	border-radius: 3px;
	line-height: 100px;
	z-index: 266;
}

.hide-ddzone {
	display: none;
}

.dropzone2 {
	cursor: pointer;
	width: 200px;
	height: 100px;
	color: gray;
	word-wrap: break-word;
}

@media screen and (max-width: 500px) {
	.profile-card {
		width: 95%;
	}
}

/* profile card */

/*#card {*/
	/*border-radius: 5px;*/
	/*-webkit-box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.24);*/
	/*-moz-box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.24);*/
	/*box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.24);*/
/*}*/

#userImage {
	position: relative;
	top: 1em;
	display: block;
	text-align: center;
	margin: 1em auto;
	max-width: 100px;
	border-radius: 100%;
}
#badge {
	margin: auto;
	position: relative;
	top: -16px;
	left: 52%;
}

#playerName {
	position: relative;
	top: -15px;
	text-align: center;
	font-size: 20px;
	color: #555;
	font-weight: bolder;
}

#states {
	width: 85%;
	margin: 1em auto;
	font-weight: 300;
}

ul {
	padding-left: 0 !important;
	padding-bottom: 1em;
	list-style: none;
}

ul li {
	width: 100%;
	color: #999;
	margin-bottom: 8px;
	padding: 1px 0px 8px 0px;
	border-bottom: 1px solid #f1f1f1;
}

ul li:last-child {
	border: none;
}

#values li:hover {
	color: #555;
	cursor: pointer;
	text-decoration: underline;
}

#info {
	width: 100%;
	position: relative;
	text-align: left;
}

#values {
	position: relative;
	text-align: right;
}



