.facebook-button, .github-button, .google-button {
	background-color: transparent;
	border: none;
	width: 32px;
	height: 32px;
	cursor: pointer;
	overflow: hidden;
	opacity: 0.5;
}

/*.github-button {*/
	/*margin: 17px 35px 0 0;*/
	/*background: url(../../lib/img/facebook-logo.png) no-repeat;*/
	/*background-size: cover;*/
/*}*/

.facebook-button {
	margin: 17px 0 0 0;
	background: url(../../lib/img/facebook-logo.png) no-repeat;
	background-size: cover;
}

.google-button {
	margin: 17px 10px 0 0;
	background: url(../../lib/img/google.png) no-repeat;
	background-size: cover;
}

.facebook-button:hover, .github-button:hover, .google-button:hover {
	opacity: 1;
	-webkit-transition: opacity 0.3s ease-in-out;
	-o-transition: opacity 0.3s ease-in-out;
	transition: opacity 0.3s ease-in-out;
}

.facebook-button:focus, .github-button:focus, .google-button:focus {
	outline: none;
}


.dropdown {
	display: inline-block;
	right: 50px;
	z-index: 999;
}

.dropdown-hover {
	position: relative;
	top: 0;
	left: 0;
}

.dropdown-overlay {
	display: none;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 1;
}

.dropdown-inner {
	position: absolute;
	right: -62px;
	width: 200px;
	opacity: 0;
	visibility: hidden;
	z-index: 2;
}

.dropdown:hover .dropdown-inner{
	opacity: 1;
	visibility: visible;
}

.dropdown:hover ~ .dropdown-overlay {
	display: block;
}

/* Demo only */

.btn {
	border: #555 solid 1px;
	cursor: pointer;
	padding: 8px 0;
	color: #8cbe71;
}

.dropdown-inner {
	min-width: 30%;
	padding: 15px;
	background: #fff;
	border: #aaa solid 1px;
	border-radius: 3px;
	-webkit-transition: opacity .3s, visibility .3s;
	-o-transition: opacity .3s, visibility .3s;
	transition: opacity .3s, visibility .3s;
}

/* user logo on Header */

.user-logo-header {
	border-radius: 50%;
	width: 50px;
	height: 50px;
}

/* notify */

.notify {
	cursor: pointer;
}


