@font-face {
	font-family: Pompadur;
	src: url(./lib/fonts/OperatorMonoBook.otf);
}

@import url('https://fonts.googleapis.com/css?family=Montserrat');

* {
	outline: none !important;
	text-decoration: none !important;
	font-family: 'Montserrat', sans-serif;
}

pre {
	font-family: Pompadur, sans-serif;
	font-size: 90%;
	line-height: 1.2em;
	white-space: pre;
	white-space: pre-wrap;
	white-space: -moz-pre-wrap;
	white-space: -o-pre-wrap;
	width: auto;
	display: block;
	clear: both;
	color: #8cbe71;
	padding: 1em 1em;
	margin: auto 40px auto 40px;
	background: #1f2018;
	border-radius: 5px;
}

.form-control {
	border-top: none !important;
	border-left: none !important;
	border-right: none !important;
	background-color: #1f2018;
	height: 35px;
	color: #8cbe71;
}


textarea[type="comment"]:focus,
input[type="text"]:focus,
input[type="name"]:focus,
input[type="password"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="time"]:focus,
input[type="week"]:focus,
input[type="number"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="search"]:focus,
input[type="tel"]:focus,
input[type="color"]:focus,
.uneditable-input:focus {
	background-color: #1f2018;
	color: #8cbe71;
	-webkit-box-shadow: 0 1px 0px rgba(0, 0, 0, 0.075) inset, 0 0 8px rgba(126, 239, 104, 0.6);
	        box-shadow: 0 1px 0px rgba(0, 0, 0, 0.075) inset, 0 0 8px rgba(126, 239, 104, 0.6);
	outline: 0 none;
}

.user-logo {
	border-radius: 50%;
	width: 50px;
	height: 50px;
	max-width: 250% !important;
}

.mm-popup__box {
	top: 15% !important;
}





